* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  max-width: 100%;
  display: block;
}

span.value {
  font-size: 25px;
  font-weight: 600;
}

div.timer {
  justify-content: center;
  align-items: center;
  gap: 30px;
  display: flex;
}

.wrapper button[data-start] {
  width: 90px;
  height: 30px;
  background-color: #7ac2aa;
  border: 2px solid rgba(0, 0, 0, 0);
  border-radius: 5px;
  font-weight: 500;
  transition: background-color .3s cubic-bezier(.4, 0, .2, 1);
}

.wrapper button[data-start]:hover {
  cursor: pointer;
  background-color: #83d4d4;
}

.flatpickr-input {
  text-align: center;
  height: 30px;
  border: 2px solid #67b4b4;
  border-radius: 5px;
  margin-bottom: 10px;
  font-size: 14px;
}

.wrapper button[disabled]:hover {
  cursor: default;
}

.wrapper button[disabled] {
  background-color: #88d2df;
  margin: 0 auto;
  transition: background-color .3s cubic-bezier(.4, 0, .2, 1);
}

.field .value {
  justify-content: center;
  display: flex;
}

.wrapper {
  text-align: center;
}

